.btn-bg {
  padding: 12px;
  background: rgba(19, 24, 35, 0.3411764705882353);
  -webkit-backdrop-filter: blur(9.17197px);
  backdrop-filter: blur(9.17197px);
  border-radius: 7.94904px;
  color: #d0dce8;
}
.btn-bg:hover {
  background-color: rgba(255, 255, 255, 0.15) !important;
}

.text-sm {
  font-size: 1.3rem;
  line-height: 1.25rem;
}
.text-3xl {
  font-size: 1.3rem;
  line-height: 2.25rem;
}
.text-5xl {
  font-size: 1.3rem;
  line-height: 1;
}

.wrapper {
  width: 100%;

  text-align: center;
  background: rgba(19, 24, 35, 0.3411764705882353);
  -webkit-backdrop-filter: blur(33px);
  backdrop-filter: blur(33px);
  border-radius: 12px;
  border: 1px solid rgba(19, 207, 207, 0.06666666666666667);
}

.wrapper-inner {
  padding: 20px 30px 30px 30px;
  color: #d0dce8;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  backdrop-filter: blur(33px);
}

.dashboard-card {
  background: rgba(19, 24, 35, 0.3411764705882353);
  -webkit-backdrop-filter: blur(33px);
  backdrop-filter: blur(33px);
  border-radius: 22.1411px;
  border: 1px solid rgba(19, 207, 207, 0.06666666666666667);
  /* padding: 32px 32px 44px; */
}
.dashboard-card-chips {
  background: rgba(19, 24, 35, 0.3411764705882353);
  -webkit-backdrop-filter: blur(33px);
  backdrop-filter: blur(33px);
  border-radius: 21.2895px;
  border: 1px solid rgba(19, 207, 207, 0.06666666666666667);
  padding: 10px 16px;
  box-shadow: 11.4753980637px 11.4753980637px 11.4753980637px 0 rgb(0 0 0 / 25%);
  color: #00ff00;
  font-size: 12px;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.body {
  
  color: #FCFCFC;
  margin: 0;
  font-size: 0.75rem;
  background: radial-gradient(circle at 17% 30%, #45110090, #00000000 24%), radial-gradient(circle at 93% 94%, #45110060, #00000000 21%);
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  line-height: 1;
  background-color: #000000;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
body .MuiSlider-rail,
body .MuiSlider-thumbColorPrimary {
  background-color: #fff;
  /* border: 1px solid rgba(0, 255, 0, 1); */
}

body .MuiSlider-track {
  border: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background: linear-gradient(
      90deg,
      rgba(8, 8, 8, 0.6) 6.84%,
      rgba(255, 48, 48, 0.94) 91.23%
    ),
    #000000;
  box-shadow: 0 0 10px rgb(0 0 0 / 20%);
}

body .MuiSlider-root{
  height: 10px;
}

.custom-container {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
}

.liquidity {
  background-color: #333333;
  background-image: url(./assets/liquidity-bg.png);
  background-size: cover;
  background-position: bottom left;
  background-repeat: no-repeat;
}

.footer {
  background-color: #333333;
}
.how-it-works {
  background-color: #333333;
  background-image: url(./assets/how-it-works-bg.png);

  background-position: bottom left;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 768px) {
  .nav-menu {
    min-width: 350p;
    overflow-x: auto;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
  .nav-menu::-webkit-scrollbar {
    display: none;
  }
  .earn-box {
    min-height: 350px;
  }
}
.btn {
  transition: all 0.3s;
}
.btn:hover {
  box-shadow: 0px 0px 8px 1px #00b800;
}

